  line-height: normal;
  letter-spacing: 1.62px;
  margin: 0 0 43px 0;
}
@media (max-width: 1199px) {
  .contact__title {
    font-size: 42px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767px) {
  .contact__title {
    margin: 0 0 50px 0;
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.contact__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}
@media (max-width: 767px) {
  .contact__list {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
    row-gap: 35px;
  }
}

.contact__item {
  padding: 15px 60px 15px 0;
  border-right: 1px solid #74819c;
  box-sizing: border-box;
}
.contact__item:nth-child(even) {
  padding: 15px 0 15px 60px;
  border-right: 0;
}
@media (max-width: 767px) {
  .contact__item:nth-child(even) {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .contact__item {
    padding: 0;
    border-right: 0;
  }
}

.contact__item-title {
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  margin: 0 0 15px 0;
}
@media (max-width: 1199px) {
  .contact__item-title {
    font-size: 20px;
    margin: 0 0 20px 0;
  }
}

.contact__item-content {
  color: #102654;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 152.34%;
}
@media (max-width: 1199px) {
  .contact__item-content {
    font-size: 18px;
  }
}

